import React, { useContext, useEffect } from 'react';
import { useHistory } from 'react-router';
import { SesionContext } from '../context/sesion';
import Tarjetas from './Tarjetas';

import { Button } from '@mui/material';

import moment from 'moment';
import 'moment/locale/es';
moment.lang('es');

export default function Aplicacion() {
    const sesionContext = useContext(SesionContext);

    const history = useHistory();

    useEffect(() => {
        if (sesionContext.sesion == null) {
            history.push('/login');
        }
    }, [sesionContext]);

    const cerrarSesion = () => {
        sesionContext.setSesion(null);
    };
    return (
        <>
            <header class="menu">
                <h2>TODO app</h2>
                <span>{moment().format('LLLL')}</span>
                <div>
                    <b>{sesionContext.sesion != null && sesionContext.sesion.usuario}</b>
                    <br />
                    <br />
                    <Button onClick={() => cerrarSesion()} variant="contained">
                        Cerrar sesion
                    </Button>
                </div>
            </header>
            <div class="todo-content">
                <div class="todo-list">
                    <Tarjetas
                        titulo="Hola mundo 8M"
                        descripcion="Aqui estoy escribiendo la descripcion de mis tareas"
                        fecha="16 de noviembre de 2021"
                    />
                    <Tarjetas descripcion="Aqui esta la tarjeta 2" titulo="Tarjeta 2" fecha="20 de noviembre del 2021" />
                    <Tarjetas
                        descripcion="Aqui esta la tarjeta 3, feliz navidad"
                        titulo="Tarjeta 3"
                        fecha="25 de diciembre del 2021"
                    />
                    <Tarjetas
                        descripcion="Aqui esta la tarjeta 4000, feliz año nuevo"
                        titulo="Tarjeta 4000"
                        fecha="31 de diciembre del 2021"
                    />
                </div>
                <div class="todo-list"></div>
            </div>
        </>
    );
}
