import React, { useState, useContext, useEffect } from 'react';

import { TextField, Button } from '@mui/material/';

import { useHistory } from 'react-router';

import axios from 'axios';
import { SesionContext } from '../context/sesion';

export default function Login() {
    //Contexto de la sesion de usuario

    const sesionContext = useContext(SesionContext);

    useEffect(() => {
        if (sesionContext.sesion != null) {
            history.push('/app');
        }
    }, [sesionContext]);

    //Declaramor el historial de la pagina
    const history = useHistory();

    //Declaramos un estado para el correo de nuestro componente Login
    const [correo, setCorreo] = useState();

    //Declaramos el estado del password para el componente Login
    const [password, setPassword] = useState();

    //Declaramos estado para mostrar el error de inicio de sesion

    const [error, setError] = useState(false);

    const iniciarSesion = () => {
        console.log('Iniciando sesion');

        //Vamos a reunir los valores del formulario
        let datos = {
            usuario: correo,
            password: password,
        };

        datos = JSON.stringify(datos);
        //Enviar los valores al api
        axios.post('https://prueba-unach.000webhostapp.com/api/usuario/consultar_user.php', datos).then((response) => {
            console.log(response);

            if (response.data != false) {
                //Inicar sesion

                //Crear una nueva sesion
                sesionContext.setSesion(response.data);

                history.push('/app');
                setError(false);
            } else {
                //Error
                setError(true);
            }
        });

        //Esperar respuesta del api y realizar el inicio de sesion o mandar error
    };

    return (
        <div class="login-container">
            <form id="formulario">
                <h2>Iniciar sesión</h2>

                {error == true && <p class="error">Error: no se pudo encontrar este usurio, verifique sus datos</p>}

                <TextField
                    label="Correo o usuario"
                    variant="filled"
                    onChange={(e) => setCorreo(e.target.value)}
                    value={correo}
                />
                <TextField
                    label="Contraseña"
                    variant="filled"
                    type="password"
                    onChange={(e) => setPassword(e.target.value)}
                    value={password}
                />
                <div>
                    <Button variant="contained" onClick={() => iniciarSesion()}>
                        Iniciar sesión
                    </Button>
                    <Button>Registrarse</Button>
                </div>
            </form>
        </div>
    );
}
