import React, { useEffect, useContext } from 'react';

import { useHistory } from 'react-router';

import { SesionContext } from '../context/sesion';

export default function SesionController() {
    const sesionContext = useContext(SesionContext);
    const history = useHistory();

    useEffect(() => {
        if (sesionContext.sesion == null) {
            history.push('/login');
        } else {
            history.push('/app');
        }
    }, [sesionContext]);
    return (
        <div>
            <h3>Redireccionando...</h3>
            hola mundo
        </div>
    );
}
