import React, { createContext, useEffect, useState } from 'react';

export const SesionContext = createContext();

export default function SesionProvider({ children }) {
    //Este estado indica si el usuario esta logeado o no
    //si el estado viene como null, quiere decir que no existe sesion
    const [sesion, setSesionCurrent] = useState(null);

    useEffect(() => {
        //Recuperando la sesion
        let localSesion = JSON.parse(localStorage.getItem('sesion'));

        console.log('Sesion recuperada: ', localSesion);

        setSesionCurrent(localSesion);
    }, []);

    const setSesion = (data) => {
        setSesionCurrent(data);

        //Guardamos la sesion en el Storage del navegador
        //Primero pasamos el JSON a un string y luego lo guardamos en el Storage
        localStorage.sesion = JSON.stringify(data);
    };

    return <SesionContext.Provider value={{ sesion, setSesion }}>{children}</SesionContext.Provider>;
}
