import React, { useEffect, useState } from 'react';

import Card from '@mui/material/Card';

export default function Tarjetas({ titulo, descripcion, fecha }) {
    //El componente inicia
    useEffect(() => {
        //Se monta el componente
        console.log('Se esta montanto el componente');

        //Cuando el componente se cargue

        return () => {
            console.log('SE ESTA DESMONTANDO EL COMPOENTEEEEEE!');
        };
    });

    return (
        <Card>
            <h3>{titulo}</h3>
            <span>{fecha}</span>

            <p>{descripcion}</p>
        </Card>
    );
}
